	module.exports = {"data":{"markdownRemark":{"html":"<p>朝の早朝、テレビでの映画「Reincarnation」は探検ビデオでした。前の段落にはいくつかの批判があるようで、興味深かったです。それは実際には起こりそうにない架空の物語です。しかし、それは社会の新しい文化的思考を反映しています。つまり、社会の変化の過程で唖然としている人もいますし、人生の味も知らない人もいます。それと同時に、人生と愛が感情にあり、良い気持ちが人生であるという感覚を強調しているので、遊んでいる人に良い気分になるダンサーがいます。おそらく現実にはありますが、一般的な現象ではありません。今日、商品経済の文化の下で、感情について話す人が少なくなり、より多くの人々が利益について話しています。時には人々は何を話すべきか他の人が話すことを知らない。常に正しい人はいますが、それは誰ですか？</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0069.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0069.md","frontmatter":{"title":"4月6日 水曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0069.md absPath of file >>> MarkdownRemark"}}

